import { Injectable } from '@angular/core';
import { Auth } from '../../auth/auth';
import { ToastrService } from 'ngx-toastr';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse, HttpErrorResponse
} from '@angular/common/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/do';
import { BASE_URL } from '../../constants/constant';

@Injectable()
export class AppInterceptor implements HttpInterceptor {
  constructor(
    private auth: Auth,
    public toastr: ToastrService
  ) { }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    let httpHeaders = this.getHeaders(this.auth.getUserLoginData());
    request = request.clone({
      setHeaders: httpHeaders
    });
    return next.handle(request).do(
      succ => {
        if (succ instanceof HttpResponse) {
          this.handleSuccess(succ);
        }
      },
      err => {
        if (err instanceof HttpErrorResponse) {
          this.handleError(request, next, err);
        }
      });
  }

  handleError(request, next, err) {
    if (err.status === 401) {
      this.handle401Error(request, next);
    } else {
      this.handleRequestError(err);
    }
  }

  handleSuccess(succ) {
    if (succ['body']['message'] !== undefined) {
      this.toastr.success(succ['body']['message']);
    }
  }

  private handle401Error(request: HttpRequest<any>, next: HttpHandler) {
    let parentContext = this;
    this.auth.refreshToken().then(
      function (data) {
        if (data) {
          let httpHeaders = parentContext.getHeaders(parentContext.auth.getUserLoginData());
          window.location.reload();
        }
      }
    );
  }

  private handleRequestError(err) {
    if (err['error']['message'] !== undefined) {
      let errors = [];
      let errorObject = err['error']['errors'];
      for (let error in errorObject) {
        if (errorObject.hasOwnProperty(error)) {
          errors.push(errorObject[error]);
        }
      }

      this.toastr.error(errors.join('<br>'), err['error']['message'], { enableHtml: true });

    }
  }


  private getHeaders(userDetails) {
    let headers = {};
    let currentUser = this.auth.getUserLoginData();
    if (currentUser) {
      headers['Authorization'] = currentUser.token_type + ' ' + currentUser.access_token;
    }
    headers['domain'] = window.location.origin;
    return headers;
  }
}
