import { Directive, ElementRef, HostListener, Renderer2, OnInit, Input } from '@angular/core';

@Directive({
  selector: '[appDraggable]'
})
export class DraggableDirective implements OnInit {

  // tslint:disable-next-line:no-input-rename
  @Input('appDraggable') item: object;
  @Input() clone = true;
  @Input() containerData = null;

  constructor(private el: ElementRef, private renderer: Renderer2) { }

  ngOnInit() {
    this.el.nativeElement.setAttribute('draggable', 'true');
  }

  @HostListener('dragstart', ['$event']) onDrag(event) {
    event.dataTransfer.setData('text', JSON.stringify({
       'item': this.item, 'options': { 'clone': this.clone, 'containerData': this.containerData } }));
  }

}
