import { Pipe, PipeTransform } from '@angular/core';
import { SharedFunctionsService } from '../../../shared/shared-services-and-components-module/services/sharedfunctions.service';

@Pipe({
  name: 'sorthelper',
  pure: false
})
export class SortHelperPipe implements PipeTransform {
  constructor(private sharedFunctionsService: SharedFunctionsService) {}

  transform(value: any, users: any, sortKey: string): any {
    if (Array.isArray(value) && value.length > 0) {
      let resources = [];
      value.forEach(assignment => {
        resources.push(users[assignment]);
      });
      const extracted = this.sharedFunctionsService
        .customArraySorter(resources, sortKey)
        .map(function(vals) {
          return vals.id;
        });
      return extracted;
    }
    return value;
  }
}
