import { Directive, ElementRef, OnInit, Input } from '@angular/core';
import { ApplicationConstant } from '../../../shared/constants/constant';

@Directive({
  selector: '[appAllocationItem]'
})
export class AllocationItemDirective implements OnInit {

  // tslint:disable-next-line:no-input-rename
  @Input('appAllocationItem') item: object;

  constructor(private el: ElementRef) { }

  ngOnInit() {
    let roles = this.item['roles'];
    let innerHtml = '';
    let innerClass = 'avatar avatar-sm';
    roles.forEach(element => {
      switch (element) {
        case ApplicationConstant.ROLES_LABLES.team_lead:
          innerHtml = 'TL';
          innerClass = 'avatar avatar-sm avatar-blue';
          break;
        case ApplicationConstant.ROLES_LABLES.project_manager:
          innerHtml = 'PM';
          innerClass = 'avatar avatar-sm avatar-red';
          break;
        case ApplicationConstant.ROLES_LABLES.developer:
          innerHtml = 'DE';
          innerClass = 'avatar avatar-sm avatar-gray-dark';
          break;
        case ApplicationConstant.ROLES_LABLES.super_admin:
          innerHtml = 'S';
          innerClass = 'avatar avatar-sm avatar-green';
          break;
        default:
          innerHtml = 'O';
          innerClass = 'avatar avatar-sm avatar-green';
          break;
      }
    });
    this.el.nativeElement.innerHTML = innerHtml;
    this.el.nativeElement.className = innerClass;
  }

}
