import { Directive, HostListener, ElementRef, OnInit, Input, Renderer2, Output, EventEmitter } from '@angular/core';

@Directive({
  selector: '[appDroppable]'
})
export class DroppableDirective implements OnInit {

  // tslint:disable-next-line:no-input-rename
  @Input('appDroppable') container;

  @Output() dropEvent = new EventEmitter();

  constructor(private el: ElementRef, private renderer: Renderer2) { }

  ngOnInit() { }

  @HostListener('dragover', ['$event']) onDragover(event) {
    event.stopPropagation();
    event.preventDefault();
  }


  @HostListener('drop', ['$event']) onDrop(event) {
    let data = JSON.parse(event.dataTransfer.getData('text'));
    this.dropEvent.emit({ parent: this.el.nativeElement, data: data });
  }

  createDraggedItem() {

  }
}
