import { Directive, Input, Output, HostListener, OnInit, ElementRef, Renderer2, EventEmitter } from '@angular/core';


@Directive({
  selector: '[appSlideAble]'
})

export class SlideAbleDirective implements OnInit {

  // tslint:disable-next-line:no-input-rename
  @Input('appSlideAble') sliderOptions;

  @Output() dropEvent = new EventEmitter();

  private registerMoveEvent = false;

  constructor(private el: ElementRef, private renderer: Renderer2) { }

  ngOnInit() {
    console.log(this.sliderOptions);
    // this.el.nativeElement.setAttribute('dopppable', 'true');
  }

  @HostListener('mousedown', ['$event']) onDragover(event) {
    console.log(event);
    this.registerMoveEvent = true;
    // event.stopPropagation();
    // event.preventDefault();
  }
  @HostListener('mousemove', ['$event']) onMouseMove(event) {
    if (this.registerMoveEvent) {
      console.log(event);
    }
    // event.stopPropagation();
    // event.preventDefault();
  }

  @HostListener('contextmenu', ['$event']) onRightClick(event) {
    event.stopPropagation();
    event.preventDefault();
  }

  @HostListener('mouseup', ['$event']) onMouseUp(event) {
    console.log(event);
    this.registerMoveEvent = false;
    // event.stopPropagation();
    // event.preventDefault();
  }


}
