import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CalendarComponent } from './calendar.component';
import { CalendarViewComponent } from './calendar/calendar-view.component';
import { CalendarRouting } from './calendar.routing';
import { DraggableDirective } from './directives/draggable.directive';
import { DroppableDirective } from './directives/droppable.directive';
import { SlideAbleDirective } from './directives/slidable.directive';
import { AllocationItemDirective } from './directives/allocation-item.directive';
import { KeysPipe } from '../../shared';
import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AppInterceptor } from '../../shared/http/interceptor/interceptor';
import { CalendarService } from './calendar.service';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SelectModule } from 'ng2-select/ng2-select';

import {
  SharedServicesAndComponentsModule
} from '../../shared/shared-services-and-components-module/shared-services-and-components-module';
import { CounterPipe } from './pipes/counter.pipe';
import { SortHelperPipe } from './pipes/sorthelper.pipe';

import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { PERFECT_SCROLLBAR_CONFIG } from 'ngx-perfect-scrollbar';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';



const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true,
  wheelSpeed: 0.09
};


@NgModule({
  imports: [
    CommonModule,
    CalendarRouting,
    BsDatepickerModule.forRoot(),
    ReactiveFormsModule,
    FormsModule,
    SelectModule,
    SharedServicesAndComponentsModule,
    PerfectScrollbarModule
  ],
  declarations: [
    CalendarComponent,
    CalendarViewComponent,
    DraggableDirective,
    DroppableDirective,
    SlideAbleDirective,
    AllocationItemDirective,
    KeysPipe,
    CounterPipe,
    SortHelperPipe
  ],
  providers: [
    HttpClient,
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    },
    { provide: HTTP_INTERCEPTORS, useClass: AppInterceptor, multi: true },
    CalendarService
  ],
  bootstrap: [CalendarComponent]
})
export class CalendarModule { }
